import '../../assets/scss/public/workcard.scss';

function WorkCard(props) {
	const work = props.data;
	return (
		<div className="workcard">
			<h2 className="workcard__name">
				<a href={work.html_url}>{work.name}</a>
			</h2>
			<p className="workcard__description">{work.description}</p>
			<span title={work.homepage} className="workcard__homepage">
				{work.homepage ? <LinkSvg /> : ''}
				<a href={work.homepage}>{work.homepage}</a>
			</span>
			<div className="workcard__info">
				<div className="workcard__info--issue" title="issue">
					<Issue /> {work.open_issues}
				</div>
				<div className="workcard__info--fork" title="fork">
					<Fork /> {work.forks_count}
				</div>
				<div className="workcard__info--star" title="star">
					<Star /> {work.stargazers_count}
				</div>
			</div>
			<div className="workcard__language">{work.language}</div>
		</div>
	);
}

export default WorkCard;

